.categoryItemFull {
    margin-top: 40px;
    margin-left: 30px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    border: 3px solid #33ccff;
    border-radius: 24px;
    .categoryItemFull_top {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        .price {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .title {
                font-size: 40px;
                line-height: 40px;
                font-weight: bold;
            }
            .pricePerHour {
                color: #33ccff;
                font-size: 60px;
                line-height: 70px;
                font-weight: bold;
            }
            .subTitle {
                font-size: 24px;
                line-height: 24px;
            }
        }
    }
    .categoryItemFull_center {
        padding-top: 25px;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        .characterization {
            padding: 20px;
            width: 100%;
            max-width: 260px;
            min-height: 252px;
            display: flex;
            flex-direction: column;
            border: 3px solid #33ccff;
            border-radius: 24px;
            .title {
                padding: 10px 0 20px 0;
                display: flex;
                color: #33ccff;
                font-size: 22px;
                line-height: 22px;
                font-weight: bold;
            }
            .characterization_item {
                padding-bottom: 3px;
                margin-bottom: 3px;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                width: 100%;
                font-size: 13px;
                position: relative;
                &::before {
                    content: "";
                    display: flex;
                    width: 100%;
                    height: 1px;
                    background-color: #323232;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                }
            }
        }
        .price {
            display: flex;
            flex-direction: column;
            width: 100%;
            max-width: 260px;
            .price_item {
                padding: 20px;
                &:first-child {
                    border: 3px solid #505050;
                    border-radius: 24px;
                    .price_amount {
                        color: #33ccff;
                    }
                }
                &:nth-child(2) {
                    border-bottom: 1px solid #505050;
                }
                .price_amount {
                    font-size: 20px;
                    line-height: 25px;
                    font-weight: bold;
                    color: #6f6e84;
                }
            }
        }
    }
    .categoryItemFull_bottom {
        padding-top: 20px;
        display: flex;
        flex-direction: column;
        .text {
            margin-bottom: 10px;
            font-size: 18px;
            line-height: 20px;
        }
        .red {
            color: red;
        }
    }
    .categoryItemFull_bottom_second {
        display: flex;
        flex-direction: column;
        .title {
            padding: 10px 0 20px 0;
            display: flex;
            font-size: 22px;
            font-weight: bold;
        }
        .categoryItemFull_bottom_second_items_container {
            padding: 20px 0;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            .items {
                margin-right: 10px;
                padding: 15px;
                min-height: 250px;
                display: flex;
                flex-direction: column;
                border: 3px solid #33ccff;
                border-radius: 24px;
                &:last-child {
                    margin-right: 0;
                }
                .title {
                    padding: 0 0 10px 0;
                    font-size: 18px;
                    text-align: center;
                }
                .item {
                    padding-bottom: 3px;
                    margin-bottom: 5px;
                    width: 100%;
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    font-size: 13px;
                    position: relative;
                    &::before {
                        margin-right: 3px;
                        content: "";
                        display: flex;
                        width: 5px;
                        height: 5px;
                        background-color: #323232;
                        border-radius: 50% 50%;
                    }
                    &::after {
                        content: "";
                        display: flex;
                        width: 100%;
                        height: 1px;
                        background-color: #323232;
                        position: absolute;
                        bottom: 0;
                        left: 0;
                    }
                }
            }
        }
        .categoryItemFull_bottom_second_text_bottom {
            padding-top: 20px;
            font-weight: bold;
            font-size: 18px;
        }
    }
    .categoryItemFull_button {
        padding-top: 30px;
        padding-bottom: 15px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        .categoryItemFull_modal {
            display: flex;
            align-items: center;
            justify-content: center;
            max-width: 280px;
            width: 100%;
            height: 50px;
            background-color: #33ccff;
            font-size: 18px;
            font-weight: bold;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}
