.aboutUs{
    padding: 220px 0;
    background: url(../../assets/img/aboutUs.jpg);
    background-size: cover;

    .aboutUs_content{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        color: #fff;
        .aboutUs_content_title{
            font-size: 42px;
            font-weight: bold;
            width: 100%;
            max-width: 480px;
            text-align: right;
        }
        .aboutUs_content_text{
            padding-top: 50px;
            font-size: 18px;
            max-width: 620px;
            text-align: right;

        }
    }
}