.category {
    padding-top: 30px;
    display: flex;
    flex-direction: row;
    .category_content {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        .category_item {
            padding: 20px;
            width: 100%;
            max-width: 300px;
            border: 3px solid #989898;
            border-radius: 12px;
            cursor: pointer;
        }
        .movers {
            display: flex;
            flex-direction: column;
            .movers_title {
                font-size: 22px;
                font-weight: bold;
                text-align: center;
            }
            .movers_main {
                padding-top: 20px;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-around;
                .movers_main_right {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    .movers_main_price {
                        margin-top: 10px;
                        width: 100px;
                        height: 100px;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        background-color: #f8f8fa;
                        color: #6f6e84;
                        border-radius: 100%;
                        .amount {
                            color: #33ccff;
                            font-weight: bold;
                            font-size: 30px;
                        }
                        .text {
                            font-size: 14px;
                            font-weight: bold;
                        }
                    }
                }
            }
            .movers_moreInfo {
                margin-top: 20px;
                padding: 20px 10px;
                display: flex;
                flex-direction: column;
                border: 3px solid #33ccff;
                border-radius: 24px;
                .movers_moreInfo_item {
                    padding-bottom: 3px;
                    margin-bottom: 8px;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    width: 100%;
                    font-size: 13px;
                    position: relative;
                    &::before {
                        content: "";
                        display: flex;
                        width: 100%;
                        height: 1px;
                        background-color: #323232;
                        position: absolute;
                        bottom: 0;
                        left: 0;
                    }
                    .price {
                        text-align: right;
                    }
                }
            }
            .movers_waste {
                margin-top: 20px;
                padding: 20px 10px;
                font-size: 16px;
                border: 3px solid #33ccff;
                border-radius: 24px;
            }
        }
        .cars {
            margin-bottom: 20px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            .cars_title {
                font-size: 20px;
                font-weight: bold;
            }
            .cars_subTitle {
                padding-top: 10px;
            }
        }
    }
}

.category_item_active {
    border-color: #33ccff !important;
}
